/* STATIC STYLES (desktop-first) */

.services {
  display: flex;
  flex-direction: column;

  gap: 5rem;
}

.services-cards {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(20rem, 1fr));

  gap: 4rem;
}

.services-card {
  place-items: center;
}

.services-card h3 {
  margin-bottom: 1rem;

  background: linear-gradient(90deg, var(--color-primary-text-dark), var(--color-secondary));
  background-clip: text;

  line-height: 1.1;
  text-align: center;

  -webkit-text-fill-color: transparent;
}

.services-card-elements p {
  margin-block: 0.3rem;
}

