@media (max-width: 1200px) {
}

@media (max-width: 992px) {
}

@media (max-width: 768px) {
  .services-cards {
    grid-template-columns: repeat(auto-fit, minmax(15rem, 1fr));

    gap: 2rem;
  }
}

